<template>
  <div>
    <div class="container ">
      <div class="header">
        <div>
          <!-- <van-nav-bar
            title="产量报表"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
          /> -->

          <div class="flex-sa date" @click="show = true">
            <div> {{str_time =='' ? '开始日期': str_time}}</div>
            -
            <div>{{end_time =='' ? '结束日期': end_time}}</div>
          </div>
        </div>

        <van-calendar
          color="#40b9ec"
          :min-date="minDate"
          :max-date="maxDate"
          v-model="show"
          type="range"
          allow-same-day="true"
          @confirm="onConfirm"
        />
        <ul class="tab">
          <li>员工</li>
          <li>日期</li>
          <li>工序</li>
          <li>数量</li>
        </ul>
      </div>
      <div class="content">
        <ul>
          <li v-for="(item, index) in list" :key="index">
            <div>{{ item.user_login }}</div>
            <div>{{ formatDates(item.completion_time) }}</div>
            <div>{{ item.pname }}</div>
            <div>{{ item.com_num }}</div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { jobdayTable } from "@api/user";
import { Icon, NavBar, Calendar, ActionSheet, Cell } from "vant";
import Loadings from "@components/Loadings";

export default {
  name: "Analysis",
  components: {
    [Icon.name]: Icon,
    Loadings,
    [Cell.name]: Cell,

    [Calendar.name]: Calendar,
    [NavBar.name]: NavBar,
  },
  props: {},
  data: function () {
    return {
      data: null,
      list: [],
      show: false,
      sshow: false,
      str_time:'',
      end_time:'',
      minDate: new Date(2020, 1, 1),
    };
  },
  created() {
    this.getdata();
  },

  mounted: function () {},
  methods: {
    formatDate(date) {
      return `${date.getMonth() + 1}/${date.getDate()}`;
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    onSelect(e) {
      if (e.name == "密码修改") {
        this.show = false;
        this.$router.replace({ path: "/change_password" });
      } else {
        this.show = false;
        localStorage.remove("ntoken");
        this.$router.replace({ path: "/" });
      }
    },
    onConfirm(date) {
      const [start, end] = date;
      console.log(start.getFullYear());
      this.show = false;
      this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`;
      this.end_time = `${start.getFullYear()}-${
          start.getMonth() + 1
        }-${start.getDate()}`;
      this.str_time = `${end.getFullYear()}-${end.getMonth() + 1}-${end.getDate()}`;
      this.getdata({
        str_time: `${start.getFullYear()}-${
          start.getMonth() + 1
        }-${start.getDate()}`,
        end_time: `${end.getFullYear()}-${end.getMonth() + 1}-${end.getDate()}`,
      });
    },

    getdata(datas = {}) {
      jobdayTable(datas)
        .then(({ data }) => {
          this.list = data.list;
        })
        .catch((e) => {
          Toast(e.msg);
        });
    },
    formatDates: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
  },
};
</script> 
<style scoped>
.setting {
  padding: 0 20px;
}
.header {
  position: fixed;
  top: 50px;
  left: 0;
  background: #fff;
  right: 0;
  z-index: 66;
}
.tab {
  background: #eee;
  padding: 12px 0;
  overflow: hidden;
  font-size: 14px;
}
.tab li {
  width: 25%;
  text-align: center;
  float: left;
}
.content {
  padding-top: 90px;
}
.content {
  background: #fff;
}
.date{
  font-size: 14px;
 line-height: 45px;
}
.content li {
  overflow: hidden;
  line-height: 40px;
  font-size: 14px;
  border-bottom: 1px solid #eee;
}
.content li div {
  width: 25%;
  text-align: center;
  font-size: 12px;
  float: left;
}
</style>
